<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left">
                <h2>Real name: {{ getUser.name }}</h2>
                <p> User name: {{ getUser.username }}</p>
                <p> Email: {{ getUser.email }}</p>
                <p> Street: {{ getUser.adress.street }}</p>
                <p> City:{{ getUser.adress.city }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import lmao from '@/users'

    export default {
        data() {
            return {
                DOD: lmao,
                currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
        },
        computed: {
            getUser() {
                return this.DOD.find((user) => user.id == this.currentId)
            }
        }
    }
</script>

